// @flow strict
import React, { useEffect } from "react";
import { Link } from "gatsby";
import "gitalk/dist/gitalk.css";
import Gitalk from "gitalk";
import loadable from "@loadable/component";
import Author from "./Author";
import Content from "./Content";
import Meta from "./Meta";
import Tags from "./Tags";
import styles from "./Post.module.scss";
import type { Node } from "../../types";

const GitalkComponent = loadable(() => import("gitalk/dist/gitalk-component"));

type Props = {
  post: Node,
};

const Post = ({ post }: Props) => {
  const { html } = post;
  const { tagSlugs } = post.fields;
  const { tags, title, date } = post.frontmatter;

  // let disqusConfig = {
  //   url: `${"https://mtosity.github.io" + slug}`,
  //   identifier: post.id,
  //   title: post.title,
  // };

  // useEffect(() => {
  //   if (window.FB) {
  //     window.FB.XFBML.parse();
  //   }
  //   // all other code is same
  // }, []);

  return (
    <div className={styles["post"]}>
      <Link className={styles["post__home-button"]} to="/">
        All Articles
      </Link>

      <div className={styles["post__content"]}>
        <Content body={html} title={title} />
      </div>

      <div className={styles["post__footer"]}>
        <Meta date={date} />
        {tags && tagSlugs && <Tags tags={tags} tagSlugs={tagSlugs} />}
        <GitalkComponent
          options={{
            clientID: "7753907d39cb8e4fd447",
            clientSecret: "0f4dd5b28e319c54bb3fd778098d436b667b28d6",
            repo: "mtosity.github.io", // The repository of store comments,
            admin: ["mtosity"],
            owner: "mtosity",
            id: "mtosity-blog-comment", // Ensure uniqueness and length less than 50
            distractionFreeMode: false, // Facebook-like distraction free mode
          }}
        />

        <Author />
      </div>
    </div>
  );
};

export default Post;
